import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Jokes from '@/views/Jokes'

Vue.use(Router)

export default new Router({
  routes :[
    {   
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/jokes',
      name: 'Jokes',
      component: Jokes
    }
  ]
});

